:root {
  --primary: #ff7315;
  --background: #f4f4f4;
  --border-radius: 8px;
}

body {
  padding: 20px;
  background: var(--background);
  color: #000000de;
  font-family: Quicksand, sans-serif;
  overflow-y: scroll;
}

* {
  word-break: break-all;
}

/* Welcome */
.sanble-welcome {
  text-align: center;
}
.sanble-welcome img {
  width: 80px;
}

/* Accordion */
.sanble-accordion {
  margin: auto;
  width: 80%;
  margin-bottom: 30px;
}
.sanble-accordion input {
  display: none;
}
.sanble-accordion-box {
  position: relative;
  background: white;
  height: 50px;
  transition: all 0.15s ease-in-out;
}
.sanble-accordion-box::before {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  pointer-events: none;
  box-shadow: 0 -1px 0 #e5e5e5, 0 0 2px rgba(0, 0, 0, 0.12),
    0 2px 4px rgba(0, 0, 0, 0.24);
}
header.sanble-accordion-box {
  background: var(--primary);
  z-index: 100;
  cursor: initial;
  box-shadow: 0 -1px 0 #e5e5e5, 0 0 2px -2px rgba(0, 0, 0, 0.12),
    0 2px 4px -4px rgba(0, 0, 0, 0.24);
}
header .box-title {
  margin: 0;
  font-weight: normal;
  font-size: 1.2rem;
  font-weight: 500;
  color: white;
  cursor: initial;
}
.box-title {
  width: calc(100% - 40px);
  height: 50px;
  line-height: 50px;
  padding: 0 20px;
  display: inline-block;
  cursor: pointer;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.box-title .badge {
  background-color: #000;
  border-radius: var(--border-radius);
  color: #fff;
  font-weight: 600;
  padding: 5px;
  font-size: 0.8rem;
  margin-right: 10px;
  text-transform: uppercase;
}
.box-title .badge.get {
  background-color: #2dd36f;
}
.box-title .badge.post {
  background-color: #3dc2ff;
}
.box-title .badge.update {
  background-color: #ffc409;
}
.box-title .badge.delete {
  background-color: #eb445a;
}
.box-content {
  width: calc(100% - 40px);
  padding: 20px;
  font-size: 0.9rem;
  color: #555555;
  display: none;
}
.box-close {
  position: absolute;
  height: 50px;
  width: 100%;
  top: 0;
  left: 0;
  cursor: pointer;
  display: none;
}
input:checked + .sanble-accordion-box {
  height: auto;
  margin: 16px 0;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.16), 0 6px 12px rgba(0, 0, 0, 0.32);
  border-radius: var(--border-radius);
}
input:checked + .sanble-accordion-box::before {
  border-radius: var(--border-radius);
}
input:checked + .sanble-accordion-box .box-content,
input:checked + .sanble-accordion-box .box-close {
  display: inline-block;
}
.arrows section .box-title {
  padding-left: 44px;
  width: calc(100% - 65px);
}
.arrows section .box-title:before {
  position: absolute;
  display: block;
  content: "\203a";
  font-size: 1.5rem;
  left: 20px;
  top: -2px;
  transition: transform 0.15s ease-in-out;
  color: rgba(0, 0, 0, 0.54);
}
input:checked + section.sanble-accordion-box .box-title:before {
  transform: rotate(90deg);
}

/* Parameters / Body */
.sanble-fields h4 {
  font-weight: 500;
  margin-top: 0;
  margin-bottom: 10px;
  padding: 2px 10px;
  background-color: rgb(0 0 0 / 6%);
  border-radius: var(--border-radius);
}
.sanble-fields .field p {
  font-weight: 500;
  margin: 0 0 5px 20px;
}
.sanble-fields .field p small {
  margin-left: 10px;
  color: #777777;
}
.sanble-fields .field .description {
  margin-bottom: 15px;
}
.sanble-fields .field .description p {
  font-size: 0.7rem;
  margin-left: 30px;
}
.sanble-fields .field .description .rules {
  margin: 0 0 0 20px;
  font-size: 12px;
}
